import Grid from "@mui/material/Grid";
import Header from "../Header";
import {Routes, Route} from 'react-router-dom';
import Home from "../../pages/Home";
import Portfolio from "../../pages/Portfolio";
import Contact from "../../pages/Contact";
import styled from "styled-components";

const ContentContainer = styled(Grid)`
  width: 100%;
  height: 100%;
  padding: 0.5%;
`;

const AppLayout = () => {
    return (
        <Grid container width={"100vw"} height={"100vh"}>
            <Grid container height={"10%"}>
                <Header/>
            </Grid>
            <ContentContainer height={"90%"}>
                <Routes>
                    <Route path="/Mateusz-pawelak-portfolio" element={<Home/>}/>
                    <Route path={"/Mateusz-pawelak-portfolio/portfolio"} element={<Portfolio/>}/>
                    <Route path={"/Mateusz-pawelak-portfolio/contact"} element={<Contact/>}/>
                </Routes>
            </ContentContainer>
        </Grid>
    )
        ;
}

export default AppLayout;